import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/IndexView.vue'
import ProductCenter from '../views/ProductCenterView.vue'
import CompanyIntroduction from '../views/CompanyIntroductionView.vue'
import ContactUs from '../views/ContactUsView.vue'
import Homepage from '../views/HomepageView.vue'




Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: '/index',
    component: HomeView,
    children: [
      {
        path: '/index',
        name: 'Homepage',
        component: Homepage,
        props: route => ({ columnId: route.query.columnId })
      },
      {
        path: "/productCenter",
        name: "productCenter",
        component: ProductCenter,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: "/companyIntroduction",
        name: 'companyIntroduction',
        component: CompanyIntroduction,
        meta: {
          requiresAuth: true
        }
      },
      {
        path: '/contactUs',
        name: 'contactUs',
        component: ContactUs
      },
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/HomepageView.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
